.maintitle {
    height: 100vh;
    align-items: center;
    position: relative;

    .title {
        z-index: 2;
        .mix__title{
            //position: relative;
            .dropdown{
                position: absolute;
                top: 18px;
                right: 464px;
            }
            .circle__points{
                display: block;
                position: absolute;
                content: '';
                width: 6px;
                height: 6px;
                border-radius: 50%;
                background-color: #E75626;
            }
            .circle__points:first-of-type{
                position: absolute;
                top: 32px;
                right: 477px;
                z-index: 6;
            }
            .circle__points:nth-of-type(2){
                top: 280px;
                right: 230px;
            }
            .circle__points:nth-of-type(3){
                top: 529px;
                right: 477px;
            }
            .circle__points:nth-of-type(4){
                top: 280px;
                right: 727px
            }
            .Dropdown-root {
                width: 80px;
                position: relative;
                z-index: 5;
            }
            .Dropdown-control{
                padding: 8px 23px 8px 10px;
                border-radius: 15px;
            }
            .Dropdown-menu{
                border-radius: 15px;
            }
            .Dropdown-arrow-wrapper{
                display: none;
            }
        }
    }
    
    h2 {
        font-style: normal;
        font-weight: 700;
        font-size: 120px;
        line-height: 120%;
        color: #FFFFFF;

        span {
            -webkit-text-fill-color: black;
            -webkit-text-stroke-color: white;
            -webkit-text-stroke-width: 0.1px;
        }
    }

    p {
        margin-top: 45px;
        max-width: 421px;
        width: 100%;
        font-family: 'Avenir Next Cyr';
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 120%;
    }

    h3 {
        font-family: 'Bebas Neue';
        font-style: normal;
        font-weight: 700;
        font-size: 32px;
        line-height: 120%;
        letter-spacing: 1px;
        color: #FFFFFF;
        margin-bottom: 42px;
    }

    .ring1 {
        position: absolute;
        top: 60px;
        left: 610px;

        &__outer2 {
            position: absolute;
            height: 532px;
            width: 532px;
            border-radius: 50%;
            background: #212124;
        }

        &__outer1 {
            height: 532px;
            width: 532px;
            border-radius: 50%;
            background: #171719;
            filter: blur(11px);
        }

        &__middle {
            height: 446px;
            width: 446px;
            border-radius: 50%;
            z-index: 1;
            background-color: #1E1E20;
        }

        &__inner2 {
            width: 384px;
            height: 384px;
            border: 1px solid #323232;
            border-radius: 50%;
        }

        &__inner1 {
            width: 370px;
            height: 370px;
            border: 1px solid #323232;
            border-radius: 50%;
        }

        img {
            width: 320px;
            height: 322px;
        }
    }

    .CircularProgressbar {
        width: 38%;
        position: absolute;
        top: 34px;
        right: 232px;
    }
    .CircularProgressbar .CircularProgressbar-path {
        stroke: #E75626;
    }
    .CircularProgressbar .CircularProgressbar-trail {
        stroke: transparent;
    }
}