.personal {
    padding-top: 72px;
    height: 100vh;
    h4 {
        margin-top: 108px;
        font-weight: 700;
        font-size: 48px;
        line-height: 120%;
        margin-bottom: 48px;
    }

    &__data {
        div:nth-child(2n+1) {
            font-weight: 700;
            font-size: 24px;
            line-height: 120%;
            margin-bottom: 8px;
        }

        div:nth-child(2n+2) {
            font-weight: 700;
            font-size: 32px;
            line-height: 120%;
            color: #E75626;
            margin-bottom: 22px;
        }
    }
}